          '<h4>Безопасность системы</h4><p>Для хостинга платформы мы используем новейшие системы.</p><p>Мы используем лучшие практики компьютерной безопасности и защищаем наши сервера новейшим программным обеспечением.</p><p>Осуществляется автоматическое резервное копирование базы данных раз в день.</p><p>Один раз в день создается резервная копия базы данных торговой платформы, после чего ее шифруют и помещают в архив.</p>',
          '<h4>Автоматическое дублирование резервных данных.</h4><p>Созданная резервная копия (база данных, файлы журналов и т. д.) сразу же отправляется на несколько удаленных друг от друга физических серверов.</p>',
          '<h4>Защита от DDoS-атак</h4><p>Мы используем автоматическую защиту от распределенных атак типа «отказ в обслуживании», чтобы внешние атаки не могли повлиять на ход торгов.</p>',
          '<h2 class="title-block">ПОЛЬЗОВАТЕЛЬСКОЕ СОГЛАШЕНИЕ</h2>',
          '<p>Настоящее Пользовательское соглашение (далее – Соглашение)  определяет условия использования разделов сайта FRSplatform.com (&quot;Сайт&quot;) и доступ к торговой платформе (&quot;Платформа&quot;).</p><p>Настоятельно просим прочесть эти Условия и не использовать Сайт или Платформу в случае, если вы не принимаете их.</p><p>Управляемая FRee Start exchange  (&quot;Сервис&quot;), платформа, дает возможность покупателям (&quot;Покупатель&quot;) и продавцам (&quot;Продавец&quot;), в целом именуемыми «Клиент», покупать и продавать в он-лайн режиме цифровые активы, называемые «Криптовалютой».</p><p>Открывая счет («Счет»), Клиент подтверждает и гарантирует, что:</p><p>1. Он ознакомился с условиями настоящего Соглашения в полном объеме и принимает настоящие Условия.</p><p>2. Его возраст составляет не меньше 18 лет и он обладает всеми юридическими правами принимать настоящие Условия и заключать сделки на платформе.</p><p>Сервис оставляет за собой право изменять, добавлять или удалять части настоящих Условий в любое время.</p><p>Рекомендуется периодически просматривать данное пользовательское соглашение, чтобы узнавать о его изменениях. Если после внесения изменений вы продолжите пользоваться Сервисами, это означает, что вы принимаете и соглашаетесь с изменениями. И таким образом, принимаете Пользовательское соглашение с внесенными изменениями. До тех пор, пока вы соглашаетесь с условиями Пользовательского соглашения, Сервис будет предоставлять вам персональное и непередаваемое ограниченное право на вход и использование Сайта и Платформы.</p><p>Соглашаясь с настоящими Условиями и с возможными изменениями, вы предоставляете Сервису право быть посредником между Продавцами и Покупателями для осуществления торговли на платформе согласно следующим пунктам, а также на выполнение функций, описанных ниже. ОПРЕДЕЛЕНИЯ</p><p>Платформа — техническая, функциональная и организационная структура, управляемая Сервисом для того, чтобы продавцы и покупатели могли заключать сделки купли-продажи Криптовалюты.</p><p>Криптовалюта — разновидность цифровой валюты, создание и контроль за которой базируются на криптографических методах.</p><p>Применяются такие алгоритмы как Proof-of-work , Proof-of-stake, Delegated Proof-of-stake</p><p>Функционирование таких систем происходит децентрализовано в распределенных компьютерных сетях.</p><p>Продавец — лицо, размещающее предложение о продаже Криптовалюты на платформе.</p><p>Покупатель — лицо, размещающее предложение о покупке Криптовалюты на платформе.</p><p>Клиент — покупатель или продавец, либо любой другой владелец счета.</p><p>Сделка — двусторонний договор между Покупателем и Продавцом для обмена Криптовалютой используя Платформу.</p><p>Сделка — двусторонний договор между Покупателем и Продавцом для обмена Криптовалютой используя Платформу.</p><p>Цена — стоимость в условных единицах, за которые Клиент готов осуществлять куплю-продажу Криптовалюты, используя Платформу для сделок.</p><p>Комиссия — сумма, которая взимается со счета Пользователя за операции Сервиса.</p>',
          '<h4>СЧЕТ КЛИЕНТА</h4><p>Клиент лично несет ответственность за сохранность информации учетной записи от третьих лиц, включая пароль и PIN код, а также за все действия и операции, осуществляемые с его счетом. Клиент обязуется немедленно уведомить Сервис о любых случаях подозрения несанкционированного использования его учетной записи или пароля. Использование рекомендаций Сервиса позволит обеспечить максимальную безопасность предоставленной Сервису информации.</p><p>Клиент не имеет права использовать любой другой счет, кроме собственного. Клиент не имеет права пытаться незаконно получить доступ к Сайту, и любая попытка сделать это или содействие в этом третьим лицам приведет к прекращению обслуживания Клиента и утери Клиентом всех денежных средств на его счете.  В такой ситуации, сервис, оставляет за собой право принять любые другие действия в отношении Клиента.</p><p>Кроме этого, Клиент лично обязуется соблюдать все требования законодательства, которые касаются противодействия легализации доходов, полученных преступным путём, и финансирования актов терроризма. При регистрации аккаунта Клиент дает согласие предоставлять Сервису правдивую и  актуальную информацию о себе, которая запрашивается Сайтом при регистрации аккаунта, а также постоянно обновлять такую информацию.</p><p>Клиент может иметь только один счет (аккаунт) и не может использовать или создавать любой счет (аккаунт), на другое имя, кроме своего собственного. При обнаружении множественных счетов (аккаунтов) одного и того же Клиента, такие счета блокируются Сервисом без предупреждения.</p><p>В случае если вышеупомянутые правила нарушаются, Сервис оставляет за собой право отказать Клиенту в предоставлении услуг без выплаты остатков на счетах.</p><p>Претензии связанные с любыми финансовыми операциям принимаются в течении 30 календарных дней с момента совершения финансовой операции. По истечении данного срока никакие претензии не принимаются.</p><p>Сервис FRSplatform.com предоставляет информацию о сделках и транзакциях только за последние 30 дней. Более давняя информация автоматически удаляется с целью снижения нагрузки на сервера.</p><p>Клиент признает и дает согласие, что с целью обеспечения  безопасности счетов Клиентов и мониторинга мошеннических операций на платформе, Сервис имеет право, по умолчанию, применять средства внутреннего контроля и программы для его осуществления, собирать и хранить любую информацию относительно Клиентов и их сделок, включая личную информацию о Клиентах, предоставленную ими при регистрации счета (аккаунта), или запрошенную Сервисом во время осуществления сделок, а также косвенную информацию, в том числе IP-адреса, информацию об используемой операционной системе, конфигурации программного обеспечения, и другую информацию, собранную с применением технологии «cookies».</p><p>Сервис оставляет за собой право потребовать от Клиента идентификационную информацию в зависимости от сумм, размещаемых на счетах или при наблюдении подозрительной активности, что может указывать на незаконные действия.</p><p>Клиент понимает и дает согласие, что в случае, если у Сервиса есть основания полагать, что через счет Клиента осуществляется отмывание средств или любая другая незаконная деятельность, Сервис имеет право приостановить действие вашего счета, а также потребовать от Клиента предоставить дополнительную информацию и нужные документы для выяснения обстоятельств.</p><p>Аккаунт Клиента, в том числе и все его сделки могут быть заморожены до момента рассмотрения документов Сервисом и принятия их как удовлетворяющих требования законодательства по противодействию мошенничества и финансированию терроризма.</p><p>Сервис имеет право запросить у Клиента предоставить любую дополнительную информацию, а также необходимые документы по просьбе любого компетентного органа или на основании любого действующего законодательства или регулирования любой страны, включая законодательство о противодействии мошенничеству и финансированию терроризма. В случае, если Клиент отказывается предоставлять запрашиваемую информацию, не предоставит ее в неполном объеме или укажет недостоверную информацию и документы, Сервис имеет право в одностороннем порядке отказать Клиенту предоставлять услуги и обслуживать счет.</p><p>Счета могут быть использованы исключительно для целей, определенных в настоящих Условиях. Счета, активность с которыми производилась более года назад с момента последнего посещения, блокируются. Для активации заблокированного аккаунта Клиенту необходимо обратиться в службу поддержки. Счет считается неактивным, если его владелец входил в свой аккаунт более года назад. Счета которые остаются не активным  на протяжении 2 лет, подлежат удалению.</p>',
          '<h4>ПЛАТФОРМА СДЕЛОК КУПЛИ-ПРОДАЖИ, ВВОДА И ВЫВОДА СРЕДСТВ</h4><p>Покупатель может размещать на Платформе предложения о покупке Криптовалюты, а Продавец может размещать предложения о продаже Криптовалюты. Цены купли-продажи Криптовалюты  выставляются Клиентом по своему усмотрению.</p><p>Клиент понимает, что  размещенные заявки о купле или продаже Криптовалюты считаются готовыми для совершения сделки с другими Клиентами. Как только Продавец и Покупатель соглашаются произвести сделку, она считается обязательной и не может быть отозвана. После проведения автоматической сделки Клиент теряет право отменить заявку о сделке купли-продажи Криптовалюты.</p><p>При проведении сделки между Покупателем и Продавцом, Сервис имеет исключительное право провести транзакцию в размере, соответствующем указанной в сделке цене за вычетом комиссии.</p><p>Пополнения средств, а также выводы средств оформленные с нарушением правил указанных в формах для пополнения и вывода средств, включая неверные реквизиты считаются утерянными и не подлежат возврату или компенсации.</p>',
          '<h4>ОБЯЗАННОСТИ СЕРВИСА</h4><p>Клиент признает и соглашается, что при проведении транзакций он торгует с другими Клиентами, а также признает, что Сервис действует только в качестве посредника в таких сделках, а не как контрагент.</p><p>Таким образом, Клиент обязуется действовать в соответствии со всеми законами и правилами, относящимися к операциям.</p><p>Со своей стороны, Сервис заявляет и гарантирует, что:</p><p>1. Будут использоваться все возможные меры для облегчения и обеспечения проведения транзакций между Клиентами.</p><p>2. Все сделки купли-продажи, сделанные на Платформе, будут проводиться анонимно.</p><p>3. Цена сделки будет рассчитываться на основе фактических соответствующих заявок, сделанных Клиентами в сочетании с действующими комиссиями.</p><p>4. С момента, когда определенные заявки на покупку и продажу Криптовалюты совпали, данные заявки не могут быть отменены.</p><p>5. Все денежные суммы и все Криптовалюты, депонированные Клиентом на его счет, будут храниться Сервисом от его имени, согласно данным, указанным в профиле.</p><p>6. Будут соблюдаться все законы и правила, касающиеся предлагаемой Платформы.</p><p>В случае если Клиент нарушит одно или более вышеперечисленных условий, Сервис имеет право деактивировать его счет и заморозить все денежные суммы, а также Криптовалюты, содержащиеся в нем.</p><p>Платформа не предназначена для предоставления услуг правового, налогового, страхового или инвестиционного характера. История заявок и торгов предоставляется исключительно для обеспечения осведомленности Пользователей о проведенных покупках и продажах, и не должна рассматриваться ими в качестве инвестиционной рекомендации от Сервиса. Клиент лично несет ответственность за принятые решения о совершении сделки.</p>',
          '<h4>ОБЯЗАННОСТИ ПОЛЬЗОВАТЕЛЯ</h4><p>При использовании Платформы Клиент обязан выполнять операции в соответствии с условиями, изложенными в настоящих Условиях.</p><p>Продавец гарантирует, что предлагаемая для продажи Криптовалюта, соответствует фактическим видам Криптовалюты.</p><p>Покупатель гарантирует, что валюта для покупки Криптовалюты, соответствует его фактическим активам и поступила из законных источников.</p><p>Клиент соглашается, не использовать Платформу для отмывания денег, финансирования терроризма или выполнения любой незаконной деятельности, которая может навредить функционированию Платформы.</p><p>Клиент обязуется самостоятельно отслеживать новости Платформы FRSplatform.com для получения своевременной информации о всех действиях произведенных Сервисом на Платформе.</p><p>Клиент обязуется уведомить службу поддержки, с указанием причины, при необходимости иметь более нескольких тысяч одновременно открытых ордеров.</p><p>Клиент разрешает Сервису удалять, открытые им ордера, находящиеся полностью вне рынка (10-кратное и более удаление от рыночной цены).</p><p>Клиент подтверждает, что при совершении сделки, Платформа отправляет и получает денежные суммы, Криптовалюту от имени Покупателя и от имени Продавца, через ИТ-системы, используемые на Платформе в момент совершения сделки.</p><p>Платформа предназначена для проведения торговых и/или операций обмена криптовалютных средств. Прямой транзит криптовалют или фиатных средств вне  платформы запрещен. В случае нарушения данного правила, аккаунты Покупателя и Продавца будут деактивированы без выплаты денежных средств.</p><p>Клиент гарантирует неразглашение информации, полученной в ходе общения с Cервисом поддержки. В случае нарушения данного правила, аккаунт Клиента будет деактивирован без выплаты денежных средств.</p><p>Клиент обязуется регулярно ознакамливаться с новостями Сервиса, публикуемыми в официальных аккаунтах Сервиса в социальных сетях. Клиент соглашается, что несет единоличную ответственность за утерю денежных средств, возникшую в результате невыполнения рекомендаций, опубликованных на  Сайте.</p><p>Клиент понимает, что предъявить какие-либо претензии к сервису FRSplatform.com (FRee Start exchange) можно, никаким иным способом, кроме как заполнив форму Службы поддержки. Сервис FRSplatform.com  (FRee Start exchange) не будет реагировать, и рассматривать вопросы и претензии Клиентов опубликованных на форумах и в социальных сетях, а так же в устной форме.</p>',
          '<h4>ИНТЕЛЛЕКТУАЛЬНАЯ СОБСТВЕННОСТЬ</h4><p>Сервис обладает всеми правами интеллектуальной собственности на все материалы размещенные на платформе. Клиент не имеет права копировать, распространять, воспроизводить, переиздавать, загружать, передавать, изменять, публиковать или иным образом использовать материалы, размещенные на платформе.</p>',
