acy.html">http://www.google.com/privacy.html</a></p>',
          '<p>Данные журнала событий</p><p>Принимая наши аналитические файлы cookie, Мы можем узнать, как вы используете наш веб-сайт, и обеспечить максимально качественное обслуживание при посещении нашего сайта.</p><p>Мы можем собирать информацию о том, какие действия Пользователь выполняет на Сайте. К такой информации относятся:</p><p>адрес интернет-протокола компьютера Пользователя («IP») тип и версия браузера посещенные Пользователем страницы нашего Сайта время и дата визита, а также время, проведенное на этих страницах другие статистические данные. Контакты и взаимодействие</p><p>Взаимодействие с «FRSplatform» осуществляется Пользователем по собственной инициативе; персональные данные, разрешённые к обработке в рамках настоящей Политики конфиденциальности, предоставляются Пользователем на собственный риск.</p><p>Персональные данные Пользователя хранятся в безопасном и конфиденциальном режиме сроком не больше, чем это необходимо в соответствии с целью их обработки и используются строго по назначению, оговоренному в настоящей Политике конфиденциальности.</p><p>Мы используем предоставляемые Пользователем данные для предоставления дополнительной информации о предлагаемых нами продуктах и услугах, а также для улучшения предоставляемых нами услуг или для разрешения возникаемых вопросов.</p><p>Кроме того, Мы можем использовать Ваши данные для сообщения Вам о важных новостях. Мы можем направлять вам сообщения, содержащие маркетинговую и рекламную информацию о наших продуктах и услугах по электронной почте.</p><p>Мы не передаем персональные данные третьим лицам.</p><p>Безопасность</p><p>«FRSplatform» прилагает максимум усилий для защиты конфиденциальности персональных данных Пользователей. Несмотря на эти усилия, направленные на обеспечение и хранение личной информации, собираемых через Сайт, Мы не можем гарантировать безопасность персональных данных во время передачи или хранения в наших системах на 100%.</p><p>Мы не можем гарантировать абсолютную безопасность ваших п